.portfolio {
  background-color: var(--second-bg-color);

  min-height: 100vh;
  padding: 5rem 15% 5rem;

  overflow: auto;
}

.portfolio-container-heading {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2vw 0 4vw 0;
}

.portfolio-heading {
  font-size: 6rem;
}

.portfolio-heading span {
  color: var(--main-color);
}

.portfolio-container-projects {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-content: center;
  gap: 2vw;
}

.portfolio-box {
  position: relative;
  border-radius: 2vw;

  width: 400px;
  height: 600px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  background-color: var(--second-bg-color);

  box-shadow: 0 0 0.5vw 0 rgb(200, 200, 200);
}

.portfolio-box-image-container {
  display: relative;
  top: 0;

  justify-content: center;
  border-radius: 2vw;

  overflow: hidden;

  box-shadow: 0 0 0.5vw 0 rgb(200, 200, 200);

  /* transition: 0.5s ease; */
}

/* .portfolio-box-image-container:hover {
  box-shadow: 0 0 1vw var(--main-color);
} */

.stock-img {
  width: 100%;
  height: 200px;
  transition: 0.75s ease-in;

  object-fit: fill;
  display: flex;
  align-items: center;
}

/* .stock-img:hover {
  transform: scale(1.05);
  transform-origin: center;
} */

.portfolio-content-text {
  display: block;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.portfolio-content h4 {
  padding: 0vw 0vw 0vw 0vw;
  font-size: 3rem;
}

.portfolio-content p {
  width: 20vw;
  margin-top: 1vw;
  font-size: 1.5rem;

  color: var(--text-color);
  font-weight: 200;
  text-align: center;
}

.portfolio-content-links {
  display: flex;
  align-items: center;
  justify-content: center;

  padding-top: 2.5vh;
  padding-bottom: 2.5vh;

  font-size: 2rem;
}

.portfolio-content-links span {
  font-size: 1.5rem;
  padding: 0.5vw;
}

.github-container {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;

  color: var(--main-color);
  transition: 0.2s;
}

.github-container:hover {
  color: var(--text-color);
}

.text-github {
  font-size: 2rem;
  margin-right: 0.5vw;
  font-weight: 600;
}

.icon-github {
  display: flex;
  align-items: center;
  font-size: 3.5rem;
}

